	module.exports = {"data":{"markdownRemark":{"html":"<h3>Описание:</h3>\n<p>Базовое упражнение для развития мышц груди. Основная нагрузка ложится на середину груди, также активно задействованы трицепс и дельты.</p>\n<p>Существует много вариантов выполнения этого упражнения. Наиболее распространенные это «грудной» и «лифтерский» жимы. Основное различие заключается в том, что в «лифтерском» варианте в работе задействовано больше мышц, но нагрузка на грудные мышцы немного уменьшается, кроме этого более безопасно для суставов и связок.</p>\n<h3>Порядок выполнения:</h3>\n<ol>\n  <li>Исходное положение: лягте спиной на горизонтальную скамью. Спина прогнута в пояснице, лопатки соединены, ноги под прямым углом в упоре. Возьмите штангу таким хватом, чтобы в нижней точке межу плечом и предплечьем был прямой угол. Держите её прямо над собой, на уровне плечей. «Лифтерский» вариант отличается тем, что необходимо практически встать на мост – ступни находятся в упоре примерно на уровне таза, который прижат к скамье.</li>\n  <li>На вдохе - опускайте штангу вниз, пока не коснетесь середины груди. Задержитесь ненадолго.</li>\n  <li>На выдохе - поднимите штангу в исходное положение. Старайтесь выполнять упражнение только усилиями мышц груди.</li>\n  <li>Проделайте необходимое количество повторений.</li>\n  <li>Верните штангу на стойку.</li>\n</ol>\n<h3>Советы:</h3>\n<ul>\n  <li>Ложитесь на скамью так, чтобы глаза находились под грифом штанги. Ляжете слишком глубоко - будете задевать стойки при жиме. Если наоборот – будет очень неудобно брать и ставить штангу со стойки.</li>\n  <li>При работе с большими весами используйте кистевые бинты, даже если они у Вас сильные. Постоянная высокая нагрузка может привести к болям в кистях.</li>\n  <li>Никогда не делайте жим, если Вы один в зале. Это может привести к печальным последствиям. В идеале просите кого-нибудь подстраховать.</li>\n  <li>Во время упражнения не бросайте штангу на грудь. Все движения должны быть контролируемы и плавны.</li>\n  <li>Старайтесь как бы оттолкнутся от штанги – так техника упражнения и работа мышц будет более правильной.</li>\n</ul>\n<h3>Видео-инструкция:</h3>\n<iframe width=\"100%\" height=\"550rem\" src=\"https://www.youtube.com/embed/sbB_0N_AfHg\" frameborder=\"0\" allow=\"autoplay; encrypted-media\" allowfullscreen></iframe>","frontmatter":{"path":"/exercise/jim-shtangi-leja","efficiency":"7","title":"Жим штанги лежа","image":"/static/0-01-0.jpg","image_2":"https://iq-body.ru/uploads/ra/755x567/training/original/жим%20лежа2.jpg","image_3":"","image_4":"","filterby":"chest","date":"July 12, 2017","injury":"8","bodypart":"Грудь","filtertype":"base","type":"Базовое","filterinventory":"stange","inventory":"Штанга","filterplace":""}}},"pathContext":{}}

